<template>
  <div class="guidepage">
    <van-swipe class="my-swipe" :loop="false" indicator-color="white" @change="show = !show">
      <van-swipe-item>
        <img class="auto-img" src="../assets/images/guidepage01.jpg" alt srcset />
      </van-swipe-item>
      <van-swipe-item>
        <img class="auto-img" src="../assets/images/guidepage02.jpg" alt srcset />
      </van-swipe-item>
      <van-swipe-item>
        <img class="auto-img" src="../assets/images/guidepage03.jpg" alt srcset />
      </van-swipe-item>
      <van-swipe-item>
        <img class="auto-img" src="../assets/images/guidepage04.jpg" alt srcset />
        <transition name="bounce">
          <div v-if="show" class="go" @click="goPage('Home')">点击进入</div>
        </transition>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
    };
  },
  methods:{
    //跳转页面
    goPage(name) {
      this.$router.push({ name });
    },
  }
};
</script>

<style lang="less" scoped>
.my-swipe .van-swipe-item {
  color: #fff;
  text-align: center;
  background-color: #39a9ed;
}
.go {
  position: absolute;
  margin: 27% 20%;
  padding: 2% 5%;
  width: 50%;
  font-size: 25px;
  bottom: 0;
  left: 0;
  border-radius: 50px;
  border: 1px solid white;
  font-family: Georgia, "Times New Roman", Times, serif;
}
.bounce-enter-active {
  animation: bounce-in 1s;
}
.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
</style>